import React, { useContext } from "react";
import styled from "styled-components";
import { MapContext } from "../Context";

const ButtonOverlay = styled.div`
  position: absolute;
  background: red;
  color: white;
  width: 20rem;
  left: 1rem;
  top: 1rem;
  text-align: center;
  height: 4rem;
  line-height: 4rem;
  font-size: 16px;
  font-family: sans-serif;
  border: 6px white solid;
  position: absolute;
  background: red;
  color: white;
  width: 4rem;
  cursor: pointer;
  border-radius: 4rem;
  opacity: 0.5;
`;

export const ReturnToMap = () => {
  const { dispatch } = useContext(MapContext);

  return (
    <ButtonOverlay onClick={() => dispatch({ type: "map" })}>MAP</ButtonOverlay>
  );
};
